<template>
  <div class="index">
    <bread-nav :nav="nav"></bread-nav>
    <main-part>
      <div class="btn-block">
        <div @click="$router.push('/edu-affair/assessment/workload/task')" class="btn-main-deepen">教学任务设置</div>
      </div>
      <search-box :searchData="searchData" :table="table"></search-box>

      <resize-table>
      <table class="table">
        <thead>
        <tr>
          <th width="15%">教师姓名</th>
          <th width="15%">年级</th>
          <th width="15%">科目</th>
          <th width="40%">教授班级</th>
          <th width="15%">总工作量</th>
        </tr></thead>
        <tbody>
        <tr v-for="(item,key) in table.list">
          <td>{{item.teacherName}}</td>
          <td>{{item.gradeName}}</td>
          <td>{{item.courseName}}</td>
          <td>{{item.classNameList.join('，')}}</td>
          <td>{{item.workloadTotal}}</td>
        </tr></tbody>
      </table>
      </resize-table>
      <no-data v-show="!table.noData"></no-data>
      <paging :data.sync="table"></paging>
    </main-part>
  </div>

</template>
<script>
  export default {
    data() {
      return {
        nav: [{name: "工作量", path: ""}],
        searchData: [
          {type: 'select', title: '学期', width: '240px', model: 'schoolTermId', option: []},
        ],
        table: {
          url: '/assessWorkload/queryAssessWorkloadPage.do',
          el: '.table',
          total: 0,
          noData: true,
          list: [],
          query: {
            schoolTermId: null,
          }
        },
      }
    },
    methods: {
      // 获取学期
      async getTermList(){
        let res = await this.$ajax({
          url: '/school/querySchoolTermList.do'
        })
        this.searchData[0].option = []
        this.searchData[0].option = res.map(e=>{
          return {label: e.termName,value: e.schoolTermId}
        })
        res.forEach(e=>{
          if(e.isDefault === 'YES'){
            this.table.query.schoolTermId = e.schoolTermId
          }
        })
      },
    },
    async mounted(){
      await this.getTermList()
      this.$search(this.table)
    }
  }
</script>
